import React from 'react';
import * as game from './game';

export const wormhole_colors = [
  '#ffff00',
  '#ff00ff',
  '#00ffff',
  '#ff0000',
  '#0000ff',
  '#00ff00',
  '#ffff88',
  '#ff88ff',
  '#88ffff',
  '#ff8888',
  '#8888ff',
  '#88ff88',
];


function drawWormholeIn(color: string, text: string) {
  return <span className="wormholeIn" style={{
    backgroundColor: color,
    height: '0.6em',
    width: '0.6em',
    display: 'inline-block',
  }}>{text}</span>;
}

function drawWormholeOut(color: string) {
  return <span className="wormholeOut" style={{
    borderColor: color,
    height: '0.6em',
    width: '0.6em',
    display: 'inline-block',
    // borderColor: 'white',
  }}></span>;
}


function birdSvg(fillColor: string) {
  return <svg width="200" height="200" viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg">
    <path d={
      [
      /* shape of head */
      "M 14 12 ",
      "A 7 7 0 1 1 23 12",
      /* body */
      "C 24.5 13, 25 14.5, 26 16",
      "S 1 1, 26 23",
      "S -1 1, 24 25",
      "S -1 0, 22 28",
      // "S -1 0, 17 28, 12 28",
      ].join(" ")
    } stroke="black" fill={fillColor}/>
  </svg>
}

type Props = {
  state: game.GameState,
  err: Error | null,
  msg: Error | null,
  extra_div: React.ReactElement | null,
  won: boolean,
  level_header: string,
  level_victory: string | null,
  level_render_classes: Array<string>,
  activePlayer: number | null,
  menu: boolean,
}
function App(props: Props) {
  const state = props.state;
  let main_div: React.ReactElement;
  if (props.err !== null) {
    main_div = (
      <div>
        Bug in game!  Contact developer: {props.err.message}
        <br/>
        {props.err.stack}
        <br/>
        Pressing any key resumes.
      </div>
    )
    throw props.err;
  } else if (props.menu) {
    main_div = (
      <div className="controls">
        <h4 style={{marginBottom: '0'}}>Controls</h4>
        <ul style={{marginTop: '10px'}}>
          <li><kbd>&lt;enter&gt;</kbd> or <kbd>p</kbd> toggles this menu</li>
          <li><kbd>r</kbd> resets level, <kbd>z</kbd> undoes, <kbd>Z</kbd> redoes</li>
          <li><kbd>b</kbd> goes back a level, <kbd>n</kbd> skips a beaten level</li>
          <li><kbd>&lt;arrow keys&gt;</kbd> or <kbd>w</kbd><kbd>a</kbd><kbd>s</kbd><kbd>d</kbd> moves the bird</li>
          <li><kbd>&lt;space&gt;</kbd> passes time</li>
          <li><kbd>c</kbd> creates an item from the future, <kbd>x</kbd> cycles through birds</li>
        </ul>
        <h4>
        Press any key to continue
        </h4>
      </div>
    )
  } else {
    main_div = (
      <React.Fragment>
        <h3 style={{marginTop: '0px'}}>
          {props.level_header}
        </h3>
        <div className={props.level_render_classes.join(" ")} style={{display: 'flex', flexDirection: 'row', justifyContent: 'center', width: 'calc(80vw)'}}>
          <div style={{padding: '0px 10px', flexGrow: 1, flexBasis: 0}}>
          {
            state.get('grid').map((row, y) => {
              return <div key={y} className='gridrow'>
                {row.map((cell, x) => {
                  let classNames: Array<string> = ['cell'];
                  let style: React.CSSProperties = {};
                  let items = cell.get('items');
                  if (items.size === 0) {
                    classNames.push('empty');
                  }
                  let children: Array<React.ReactNode> = [];
                  items.forEach((item, i) => {
                    switch (game.getItemType(item)) {
                      case 'player': {
                        let id = (item as game.Player).get('id');
                        let pClassName = (id === props.activePlayer) ? "player active" : "player";
                        let deg: number;
                        switch ((item as game.Player).get('facing')) {
                          case 'right':
                            deg = 90;
                            break;
                          case 'left':
                            deg = 270;
                            break;
                          case 'up':
                            deg = 0;
                            break;
                          case 'down':
                            deg = 180;
                            break;
                          default:
                            throw new Error(`Unknown facing: ${(item as game.Player).get('facing')}`);
                        }
                        let bg = `url(${process.env.PUBLIC_URL + '/img/bird-top.png'})`;
                        if (id !== props.activePlayer) {
                            // darken
                            bg = `linear-gradient(#160A2255, #160A2255), ${bg}`
                        }
                        children.push(
                          <div key={i} className={pClassName} style={{
                            // rotated image
                            transform: 'rotate(' + deg + 'deg)',
                            backgroundImage: bg,

                          }}/>
                        )
                        break;
                      }
                      case 'rock':
                        children.push(
                          <div key={i} className="rock" style={{}}/>
                        );
                        break;
                      case 'wall':
                        classNames.push('wall');
                        break;
                      case 'exit':
                        classNames.push('exit');
                        break;
                      case 'enter':
                        children.push(
                          <div key={i} className="enter" style={{}}/>
                        )
                        break;
                      case 'wormholeIn': {
                        let text: string;
                        let id = (item as game.WormholeIn).get('id');
                        let t: number = state.getIn(['wormholes', id, 'outTime']) as number;
                        if (t === 0) {
                          text = ''
                        } else if (t > 0) {
                          text = `t+${t}`
                        } else {
                          text = `t-${-t}`
                        }
                        children.push(
                          <div key={i} className="wormholeIn" style={{
                            width: '100%', height: '100%',
                            backgroundColor: wormhole_colors[(item as game.WormholeIn).get('id')],
                          }}><span className="text">{text}</span></div>
                        );
                        break;
                      }
                      case 'wormholeOut':
                        children.push(
                          <div key={i} className="wormholeOut" style={{
                            width: '100%', height: '100%',
                            borderColor: wormhole_colors[(item as game.WormholeOut).get('id')],
                          }}/>
                        );
                        break;
                      case 'shiny':
                        children.push(
                          <div key={i} className="shiny" style={{}}/>
                        );
                        break;
                          /*
                      case 'twig':
                        children.push(
                          <div key={i} className="twig" style={{}}/>
                        );
                        break;
                           */
                      default:
                        throw new Error(`Unknown item type: ${game.getItemType(item)}`);
                    }
                  });
                  return <span key={x} className={classNames.join(' ')} style={style}>
                    {children}
                  </span>;
                })}
              </div>;
            })
          }
              t={game.getTime(state)}
          </div>
          <div style={{padding: '0px 10px', flexGrow: 1, flexBasis: 0}}>
            {null /*props.level_victory ? <div>{props.level_victory}</div> : null */}
            {props.msg ? <div style={{color: 'red'}}>{props.msg.message}</div> : null}
            {props.extra_div}
              {
                  props.won ?
                  <div><br/>Press any key to continue</div> :
                  <div>
                    <h4 style={{margin: '0px'}}>Events</h4>
                    <ul style={{margin: '0px'}}>
                      {
                      props.state.get('events').reverse().map((event, y) => {
                      let eventdiv: React.ReactNode;
                      switch (game.getEventType(event)) {
                        case 'collect':
                          eventdiv = `The bird pushes a ${(event as game.CollectToExitEvent).get('collect')} to the exit`;
                          break;
                        case 'exit': {
                          let t = (event as game.ReachExitEvent).get('time')
                          if (t) {
                            eventdiv = `The bird exits the level before t=${t}`;
                          } else {
                            eventdiv = `The bird exits the level`;
                          }
                          break;
                        }
                        case 'exit_wormhole': {
                          // TODO show which wormhole
                          let ex_ev = (event as game.ExitWormholeEvent)
                          eventdiv = `${game.item_display(ex_ev.get('item'), true)} exits the wormhole at t=${ex_ev.get('time')}`;
                          break;
                        }
                        case 'enter_wormhole': {
                          // TODO show which wormhole
                          let en_ev = (event as game.EnterWormholeEvent)
                          let dir = en_ev.get('direction');
                          if (dir === null) {
                            eventdiv = `${game.item_display(en_ev.get('item'), true)} enters the wormhole at t=${en_ev.get('time')+1}`;
                          } else {
                            eventdiv = `${game.item_display(en_ev.get('item'), true)} goes ${dir} to enter the wormhole at t=${en_ev.get('time')+1}`;
                          }
                          break;
                        }
                        default:
                          throw new Error(`Unknown event type: ${game.getEventType(event)}`);
                      }
                      return <li key={y} className='event'>
                        {eventdiv}
                        </li>;
                        })
                        }
                    </ul>
                    {/*
                      <h4 style={{marginBottom: '0px'}}>Birds</h4>
                      <ul style={{margin: '0px'}}>
                        {(() => {
                          let player_ids = Array.from(props.state.get('players').keys());
                          player_ids.sort();
                          return player_ids.map((player_id, y) => {
                            // let playerState: game.PlayerState = props.state.getIn(['players', player_id]) as game.PlayerState;
                            // let t = game.getPlayerTime(playerState);
                            return <div key={y} className='bird'>
                              <span className='cell' style={{
                                backgroundImage: `url(${process.env.PUBLIC_URL + '/img/bird-right.png'})`,
                              }}></span>
                            t={t}
                            </div>;
                          })
                          }
                        )()}
                      </ul>
                      */}
                  </div>
              }
            {null/*birdSvg('#ffff00')*/}
        </div>
        </div>
      </React.Fragment>
    );
  }
  return (
    <div className="App">
        <h2 style={{fontSize: (props.err || props.menu) ? 'calc(4vw)' : 'calc(3vw)', marginBottom: '5px'}}>
          THE W
          <img src={process.env.PUBLIC_URL + '/img/bird-right-body.png'} alt="bird" style={{position: 'absolute', height: '0.6em', marginLeft: '-0.00em', marginTop: '0.3em'}}/>
          {drawWormholeIn('#ffff00', '')}
          RMH
          <img src={process.env.PUBLIC_URL + '/img/bird-right-head.png'} alt="bird" style={{position: 'absolute', height: '0.6em', marginLeft: '-0.00em', marginTop: '0.3em'}}/>
          {drawWormholeOut('#ffff00')}
          LE GETS THE BIRD EARLY
        </h2>
        {main_div}
    </div>
  );
}

export default App;
