import { List, Map } from 'immutable';

import { ImmutableMap, Optional, Result } from './utils';

export type Direction = 'up' | 'down' | 'left' | 'right';
// not immutable
export type Location = ImmutableMap<{x: number, y: number}>;
export type Player = ImmutableMap<{type: 'player', id: number, facing: Direction}>;
export type Rock = ImmutableMap<{type: 'rock'}>;
// difference from rock is that player can be interposed with shiny
export type Shiny = ImmutableMap<{type: 'shiny'}>;
export type Wall = ImmutableMap<{type: 'wall'}>;
export type Enter = ImmutableMap<{type: 'enter'}>;
export type Exit = ImmutableMap<{type: 'exit'}>;
export type WormholeIn = ImmutableMap<{type: 'wormholeIn', id: number}>;
export type WormholeOut = ImmutableMap<{type: 'wormholeOut', id: number}>;

export type Item = Player | Rock | Wall | Exit | WormholeIn | WormholeOut | Shiny;
export type ItemType = 'player' | 'rock' | 'wall' | 'enter' | 'exit' | 'wormholeIn' | 'wormholeOut' | 'shiny';
export function getItemType(item: Item): ItemType {
  return (item as any).get('type');
}
export function pushable(item: Item): boolean {
  return ['shiny', 'rock', 'player'].includes(getItemType(item));
}
export function equivalent_item(item1: Item, item2: Item): boolean {
  if (getItemType(item1) !== getItemType(item2)) {
    return false;
  }
  let t = getItemType(item1);
  if (t === 'wormholeIn' || t === 'wormholeOut') {
    return (item1 as any).get('id') === (item2 as any).get('id');
  }
  return true;
}

// TODO, have dinosaurs and stuff

export type WormholeState = ImmutableMap<{in: Location, out: Location, outTime: number}>;

export type Cell = ImmutableMap<{
  items: List<Item>,
}>;
export type Grid = List<List<Cell>>;

export type EnterWormholeEvent = ImmutableMap<{
  type: 'enter_wormhole',
  wormhole_id: number,
  time: number,
  item: Item,
  direction: Direction | null,
}>;
export type ExitWormholeEvent = ImmutableMap<{
  type: 'exit_wormhole',
  wormhole_id: number,
  time: number,
  item: Item,
  direction: Direction,
}>;
export type CollectToExitEvent = ImmutableMap<{
  type: 'collect',
  collect: string,
  // TODO: track wormholes by ID
}>;
export type ReachExitEvent = ImmutableMap<{
  type: 'exit',
  time: number,
}>;
export type Event = EnterWormholeEvent | ExitWormholeEvent | CollectToExitEvent | ReachExitEvent;
export type EventType = 'enter_wormhole' | 'exit_wormhole' | 'collect' | 'exit';
export function getEventType(event: Event): EventType {
  return (event as any).get('type');
}

export type PlayerState = ImmutableMap<{
  location: Location | null,
}>

export type GameState = ImmutableMap<{
  grid: Grid,
  past: List<GameState>,
  // for each player, a list of indices into the history of game states
  players: Map<number, PlayerState>,
  wormholes: Map<number, WormholeState>,
  events: List<Event>,
}>;

export function existingPlayers(state: GameState): List<number> {
  return state.get('players').filter(p => p.get('location') !== null).keySeq().toList();
}

export function getTime(state: GameState): number {
  return state.get('past').size;
}
export function isWon(state: GameState): boolean {
   return state.get('events').size === 0
}
// export type GameState = List<>;

export function getCell(grid: Grid, loc: Location): Cell {
  return grid.getIn([loc.get('y'), loc.get('x')]) as Cell;
}
export function updateGrid(grid: Grid, loc: Location, f: (cell: Cell) => Cell): Grid {
  return grid.updateIn([loc.get('y'), loc.get('x')], f as any);
}
export function updateCell(cell: Cell, f: (items: List<Item>) => List<Item>): Cell {
  return cell.update('items', (f as any));
}
export function cellUpdater(f: (items: List<Item>) => List<Item>): (cell: Cell) => Cell {
  return (cell: Cell) => updateCell(cell, f);
}

