export type MoveAction = ImmutableMap<{
  type: 'move',
  playerId: number,
  direction: Direction,
}>;
export type WaitAction = ImmutableMap<{
  type: 'wait',
}>;
export type EnterAction = ImmutableMap<{
  type: 'enter',
  item: ItemType,
  wormhole_id: number,
  direction: Direction | null,
}>;

export type Action = MoveAction | WaitAction | EnterAction;
export type ActionType = 'move' | 'wait' | 'enter';

export function getActionType(action: Action): ActionType {
  return (action as any).get('type');
}


export function moveLocation(grid: Grid, location: Location, direction: Direction): Result<Optional<Location>> {
  let new_location;
  switch (direction) {
    case 'up':
      new_location = location.update('y', (y: number) => y - 1);
      if (new_location.get('y') < 0) {return [null, null]}
      break;
    case 'down':
      new_location = location.update('y', (y: number) => y + 1);
      if (new_location.get('y') > grid.size - 1) {return [null, null]}
      break;
    case 'left':
      new_location = location.update('x', (x: number) => x - 1);
      if (new_location.get('x') < 0) {return [null, null]}
      break;
    case 'right':
      new_location = location.update('x', (x: number) => x + 1);
      if (new_location.get('x') > (grid.get(0) as any).size - 1) {return [null, null]}
      break;
    default:
      return [null, new Error(`Unknown direction ${direction}`)];
  }
  return [new_location, null];
}

export function exitWormhole(state: GameState, pushed_item: Item, wormhole_id: number, direction: Direction): Result<Optional<GameState>> {
  let wormhole: WormholeState | null = state.getIn(['wormholes', wormhole_id]) as WormholeState;
  if (!wormhole) {return [null, new Error(`Wormhole was missing at time ${getTime(state)}`)]}
  let new_location = wormhole.get('out')
  let [push_state, err] = tryPush(state, pushed_item, new_location, direction);
  if (push_state === null) {
    return [null, err];
  }
  let new_state = push_state;
  if (getItemType(pushed_item) === 'player') {
    let playerId = (pushed_item as Player).get('id');
    new_state = new_state.setIn(['players', playerId, 'location'], new_location)
  }
  return [new_state, null]
}

export function tryWormhole(state: GameState, pushed_item: Item, wormhole_id: number, direction: Direction): Result<Optional<GameState>> {
  let wormhole: WormholeState | null = state.getIn(['wormholes', wormhole_id]) as WormholeState;
  if (!wormhole) {return [null, new Error(`No wormhole with id ${wormhole_id}`)]}
  let dt = wormhole.get('outTime');
  let new_state: GameState;
  if (dt > 0) {
    // item simply goes away.
    new_state = state.update('events', (events: List<Event>) => events.push(
      Map({
        type: 'exit_wormhole',
        wormhole_id: wormhole_id,
        time: getTime(state) + dt + 1,
        item: pushed_item,
        direction: direction,
      })
    ));
    if (getItemType(pushed_item) === 'player') {
      let playerId = (pushed_item as Player).get('id');
      new_state = new_state.setIn(['players', playerId, 'location'], null)
    }
    return [new_state, null];
  } else if (dt < 0) {
    let event = state.get('events').find((event: Event) => {
      if (getEventType(event) !== 'enter_wormhole')  {
        return false;
      }
      let enter_wormhole_event = event as EnterWormholeEvent;
      if (wormhole_id !== enter_wormhole_event.get('wormhole_id')) {
        return false;
      }
      if (getTime(state) !== enter_wormhole_event.get('time')) {
        return false;
      }
      if (!equivalent_item(pushed_item, enter_wormhole_event.get('item'))) {
        return false;
      }
      let enter_direction = enter_wormhole_event.get('direction');
      if (enter_direction !== null) {
        if (direction !== enter_direction) {
          return false;
        }
      }
      return true;
    });
    if (event === undefined) {
      // let msg = `${item_display(pushed_item, true)} does not enter a wormhole at time ${getTime(state)}`;
      let msg = `${item_display(pushed_item, true)} does not exit a wormhole ${direction} at time ${getTime(state) + dt}.  Use 'c' to create items.`;
      return [null, new Error(msg)];
    } else {
      new_state = removeEvent(state, event);
      if (getItemType(pushed_item) === 'player') {
        let playerId = (pushed_item as Player).get('id');
        new_state = new_state.setIn(['players', playerId, 'location'], null)
      }
      return [new_state, null];
    }
  } else {
    new_state = state;
  }
  return exitWormhole(new_state, pushed_item, wormhole_id, direction);
}

export function removeEvent(state: GameState, event: Event): GameState {
  let new_state = state.set('events', state.get('events').filter(e => e !== event));
  if (new_state.get('events').size !== state.get('events').size - 1) {
    throw new Error(`Strange, removed multiple events`);
  }
  return new_state;
}

export function tryPush(state: GameState, pushed_item: Item, new_location: Location, direction: Direction): Result<Optional<GameState>> {
  let grid = state.get('grid');
  let new_state: GameState = state;
  console.log(`pushing ${pushed_item} ${direction} to ${new_location.get('x')},${new_location.get('y')}`);
  const cell = getCell(grid, new_location);
  const items = cell.get('items');
  let has_wormhole = items.filter((item: Item) => getItemType(item) === 'wormholeIn').size;
  let has_exit = items.filter((item: Item) => getItemType(item) === 'exit').size;
  if (has_wormhole) {
    if (items.size > 1) {
      throw new Error(`Wormhole In has multiple items ${items}`);
    }
    let wormhole_id: number = (items.first() as WormholeIn).get('id');
    console.log(`Pushed ${pushed_item} ${direction} through wormhole ${wormhole_id}`);
    return tryWormhole(state, pushed_item, wormhole_id, direction);
  } else if (has_exit) {
    let event = state.get('events').find((event: Event) =>
      (getEventType(event) === 'collect' && (event as CollectToExitEvent).get('collect') === getItemType(pushed_item))
      // TODO: respect time
      || (getEventType(event) === 'exit' && getItemType(pushed_item) === 'player')
    );
    if (event === undefined) {
      return [null, new Error(`${item_display(pushed_item, true)} does not exit`)];
    } else {
      new_state = removeEvent(new_state, event);
    }
    if (getItemType(pushed_item) === 'player') {
      // set player location to null
      new_state = new_state.setIn(['players', (pushed_item as Player).get('id'), 'location'], null);
    }
    return [new_state, null];
  } else {
    let blocked = items.filter((item: Item) => ['wall'].includes(getItemType(item))).size;
    if (blocked) {
      return [null, null]; // new Error(`Could not push ${direction} ${items}`)];
    }
    let new_pushed_items = items.filter((item: Item) => pushable(item));
    let unpushed_items = items.filter((item: Item) => !pushable(item));
    if (new_pushed_items.size) {
        if (new_pushed_items.size > 1) {
          throw new Error(`Multiple items unexpectedly pushed ${new_pushed_items}`);
        }
        let new_pushed_item = new_pushed_items.first() as Item;

        let [newer_location, move_err] = moveLocation(state.get('grid'), new_location, direction);
        if (newer_location === null) {
          return [null, move_err];
        }
        let [maybe_new_state, maybe_err] = tryPush(new_state, new_pushed_item, newer_location, direction);
        if (maybe_err) {return [null, maybe_err]}
        if (maybe_new_state === null) {
          return [
            null, null // new Error(`${itemType} could not be pushed ${direction}`)
          ]
        }
        new_state = maybe_new_state;
        let grid = new_state.get('grid');
        // TODO: make sure items are still in the same location?
        // alternatively, remove the items from the grid before tryPush.  this allows "cyclical" pushing
        // could make a level based on that
        grid = updateGrid(grid, new_location, cellUpdater(items => unpushed_items));
        new_state = new_state.set('grid', grid);
    }

    let new_grid = new_state.get('grid');
    new_grid = updateGrid(new_grid, new_location, cellUpdater(items => items.push(pushed_item)));
    new_state = new_state.set('grid', new_grid);
    if (getItemType(pushed_item) === 'player') {
      let playerId = (pushed_item as Player).get('id');
      new_state = new_state.setIn(['players', playerId, 'location'], new_location)
    }
  }

  return [new_state, null];
}

export function tryMove(state: GameState, action: MoveAction): Result<Optional<GameState>> {
  let playerId = action.get('playerId');
  let player: PlayerState | undefined = state.getIn(['players', playerId]) as PlayerState | undefined;
  if (player === undefined) {
    return [null, new Error(`Player ${playerId} not found`)];
  }

  const location = player.get('location');
  const direction = action.get('direction');
  if (location === null) {
    return [null, null];
  }
  let [new_location, move_err] = moveLocation(state.get('grid'), location, direction);
  if (new_location === null) {
    return [null, move_err];
  }
  let new_player: Player = Map({type: 'player', id: playerId, facing: direction});
  let [push_state, pushError] = tryPush(state, new_player, new_location, direction);
  if (push_state === null) {
    return [null, pushError];
  }
  let new_state = push_state;
  let grid = new_state.get('grid');
  // TODO: make sure the player is still in the same location?
  // alternatively, remove the player from the grid before tryPush.  this allows "cyclical" pushing
  // could make a level based on that
  grid = updateGrid(grid, location, cellUpdater(items => items.filter(item => getItemType(item) !== 'player' || (item as Player).get('id') !== playerId)));
  new_state = new_state.set('grid', grid);

  return [new_state, null];
  // let items = cell.get('items');
  // let newItems = items.filter((item) => {
  //   return getItemType(item) !== 'player';
  // }).push(player);
  // let newCell = updateCell(cell, (items) => items.filter((item) => {
  //   return getItemType(item) !== 'player';
  // }));
  // let newGrid = updateGrid(grid, location, (cell) => newCell);
  // let newState = state.set('grid', newGrid);
  // return newState;
}

export function item_type_display(t: ItemType, capitalized: boolean) {
  let display: string
  if (t === 'player') {
    display = 'the bird';
  } else {
    display = `a ${t}`;
  }
  if (capitalized) {
    return display[0].toUpperCase() + display.slice(1);
  }
  return display;
}

export function item_display(item: Item, capitalized: boolean) {
  // TODO: display portals better?
  const t = getItemType(item)
  return item_type_display(t, capitalized)
}

export function creatables(state: GameState): List<ItemType> {
  return List(['player', 'shiny', 'rock'])
}


function tryTriggerTimeEvent(state: GameState, event: Event): [boolean, GameState, Optional<Error>] {
  switch (getEventType(event)) {
    case 'collect':
      return [false, state, null];
    case 'exit':
      // TODO
      let exitevent: ReachExitEvent = event as ReachExitEvent;
      return [false, state, null];
    case 'enter_wormhole': {
      let enter_wormhole_event: EnterWormholeEvent = event as EnterWormholeEvent;
      if (getTime(state) > enter_wormhole_event.get('time')) {
        let msg = `${item_display(enter_wormhole_event.get('item'), true)} does not enter the wormhole on time`
        return [false, state, new Error(msg)];
      }
      return [false, state, null];
    }
    case 'exit_wormhole':
      // TODO
      let exit_wormhole_event: ExitWormholeEvent = event as ExitWormholeEvent;
      if (getTime(state) > exit_wormhole_event.get('time')) {
        throw new Error(`Unexpected:  t=${getTime(state)} already past exit wormhole time ${exit_wormhole_event.get('time')}`)
      }
      if (getTime(state) < exit_wormhole_event.get('time')) {
        return [false, state, null];
      }
      let location: Optional<Location> = state.getIn(
        ['wormholes', exit_wormhole_event.get('wormhole_id'), 'out']
      ) as Optional<Location>;
      let item: Item = exit_wormhole_event.get('item');
      if (!location) {
        return [false, state, new Error(`Wormhole missing, ${item_display(item, false)} could not exit`)]
      }
      let [maybe_new_state, exit_err] = tryPush(
        state, exit_wormhole_event.get('item'), location, exit_wormhole_event.get('direction')
      )
      if (exit_err) {
        return [false, state, exit_err]
      }
      if (maybe_new_state === null) {
        return [false, state, new Error(`${item_display(item, true)} could not exit wormhole`)]
      }
      return [true, maybe_new_state, null];
    default:
      throw new Error(`Unknown event type ${getEventType(event)}`);
  }
}

export function tryAction(state: GameState, action: Action): Result<Optional<GameState>> {
  let new_state: GameState, err: Error | null;
  switch (getActionType(action)) {
    case 'move': {
      let maybe_new_state;
      [maybe_new_state, err] = tryMove(state, action as MoveAction);
      if (err !== null) {
        return [null, err];
      }
      if (maybe_new_state === null) {
        new_state = state; // basically just wait
      } else {
        new_state = maybe_new_state;
      }
      break;
    }
    case 'wait': {
      new_state = state;
      break;
    }
    case 'enter': {
      let maybe_new_state;
      let enter_action: EnterAction = action as EnterAction;
      let dir: Direction | null = enter_action.get('direction');
      let wormhole_id: number = enter_action.get('wormhole_id');
      let item: Item = Map({'type': enter_action.get('item')});
      if (getItemType(item) === 'player') {
        item = item.set('facing', dir || 'up');
        let next_id = state.get('players').keySeq().reduce((acc: number, curr: number) => {
          return Math.max(acc, curr);
        }) + 1;
        item = item.set('id', next_id);
      }
      [maybe_new_state, err] = exitWormhole(state, item, wormhole_id, dir || 'up');
      if (maybe_new_state === null) {
        return [null, err];
      }
      new_state = maybe_new_state;
      new_state = (new_state as any).update('events',
        (events: List<Event>) => events.push(
          Map({
            type: 'enter_wormhole', item: item, wormhole_id: wormhole_id, direction: dir,
            time: getTime(new_state) - (new_state.getIn(['wormholes', wormhole_id, 'outTime']) as number)
          })
        )
      );
      break;
    }
    default:
      return [null, new Error(`Unknown action type: ${getActionType(action)}`)];
  }
  new_state = new_state.update('past', (past: List<GameState>) => past.push(state));

  let remaining_events: List<Event> = List();
  let errors: List<Error> = List();
  new_state.get('events').forEach((event: Event) => {
    let [triggered, maybe_new_state, err] = tryTriggerTimeEvent(new_state, event);
    if (triggered) {
      new_state = maybe_new_state;
    } else {
      remaining_events = remaining_events.push(event);
    }
    if (err) {
      errors = errors.push(err);
    }
  });
  new_state = new_state.set('events', remaining_events);
  if (errors.size > 0) {
    return [null, errors.first()];
  }

  // TODO: trigger events for the timestep
  return [new_state, null];
}
