import { List, Map } from 'immutable';

import { ImmutableMap, Optional } from './utils';
import * as game from './game';

export type Level = ImmutableMap<{
  name: string,
  render_classes: Array<string>,
  flavor_text: string,
  help_text: string,
  initial_state: game.GameState,
  solution: Optional<List<game.Action>>,
}>;

const parse_direction = (direction: string): game.Direction => {
  switch (direction) {
    case 'u': return 'up';
    case 'd': return 'down';
    case 'l': return 'left';
    case 'r': return 'right';
    default: throw new Error(`Unknown direction: ${direction}`);
  }
}

const parse_solution = (sol: string): List<game.Action> => {
  let playerId = 0;
  let it = sol.split('').entries();
  let result = List<game.Action>();
  while (true) {
    let {value, done} = it.next();
    if (done) {
      break;
    }
    let c = value[1];
    switch (c) {
      case '(':
      case ')':
        break;
      case 'u':
      case 'd':
      case 'l':
      case 'r':
        result = result.push(Map({type: 'move', playerId, direction: parse_direction(c)}));
        break;
      case ' ':
        result = result.push(Map({type: 'wait'}));
        break;
      case '0':
      case '1':
      case '2':
        playerId = parseInt(c, 10);
        break;
      case 'c': {
        let wormhole_id = parseInt(it.next().value[1]);
        let itemMap: {[key: string]: game.ItemType} = {'b': 'player', 's': 'shiny', 'r': 'rock'};
        let t: game.ItemType = itemMap[it.next().value[1] as string];
        let direction = parse_direction(it.next().value[1]);
        result = result.push(Map({type: 'enter', wormhole_id, item: t, direction}));
        // let type = it.next().value[1];
        break;
      }
      default: {
        throw new Error(`Unknown character '${c}'`);
      }
    }
  }
  return result;
}

const parse_level = (
  name: string,
  flavor_text: string,
  help_text: string,
  render_classes: Array<string>,
  gridstr: string,
  wormholes: Array<game.WormholeState>,
  events: Array<game.Event>,
  player_direction: game.Direction,
  solution_str: string | null,
): Level => {
  let playerId = 0;
  gridstr = gridstr.trim();
  let grid = List<List<game.Cell>>();
  let lines = gridstr.trim().split('\n');
  let players = Map<number, game.PlayerState>();
  lines.forEach((line, y: number) => {
    let row = List<game.Cell>();
    Array.from(line).forEach((c: string, x: number) => {
      let cell = Map({
        items: List<game.Item>(),
      });
      switch (c) {
        case '#':
          cell = game.updateCell(cell, (items) => items.push(Map({type: 'exit'})));
          break;
        case 'x':
          cell = game.updateCell(cell, (items) => items.push(Map({type: 'wall'})));
          break;
        case 'o':
          cell = game.updateCell(cell, (items) => items.push(Map({type: 'rock'})));
          break;
        case '@':
          cell = game.updateCell(cell, (items) => items.push(Map({type: 'player', id: playerId, facing: player_direction})));
          cell = game.updateCell(cell, (items) => items.push(Map({type: 'enter'})));
          players = players.set(playerId, Map({
            location: Map({
              x, y,
            }),
          }));
          playerId++;
          break;
        case '$':
          cell = game.updateCell(cell, (items) => items.push(Map({type: 'shiny'})));
          break;
        case '/':
          cell = game.updateCell(cell, (items) => items.push(Map({type: 'twig'})));
          break;
        case ' ':
          break;
        default:
          throw new Error(`Unknown character '${c}'`);
      }
      row = row.push(cell);
    });
    grid = grid.push(row);
  });
  if (lines.length <= 7) {
    render_classes.push('big-cells');
  } else if (lines.length <= 11) {
    render_classes.push('medium-cells');
  }
  let wormholes_tup: Array<[number, game.WormholeState]> = [];
  let wormhole_id = 0;
  wormholes.forEach((wormhole: game.WormholeState) => {
    let location = wormhole.get('in');
    grid = game.updateGrid(grid, location, game.cellUpdater((items) => items.push(Map({
      type: 'wormholeIn',
      id: wormhole_id,
    }))));
    location = wormhole.get('out');
    grid = game.updateGrid(grid, location, game.cellUpdater((items) => items.push(Map({
        type: 'wormholeOut',
        id: wormhole_id,
    }))));
    wormholes_tup.push([wormhole_id, wormhole]);
    wormhole_id++;
  });
  const gameState = Map({
    grid,
    events: List<game.Event>(events),
    wormholes: Map<number, game.WormholeState>(wormholes_tup),
    players,
    past: List<game.GameState>(),
  });
  let solution: List<game.Action> | null = null;
  if (solution_str !== null) {
    solution = parse_solution(solution_str);
  }
  return Map({
    name,
    flavor_text,
    help_text,
    initial_state: gameState,
    render_classes,
    solution,
  });
}


