import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App, { wormhole_colors } from './App';
import reportWebVitals from './reportWebVitals';
import { List, Map } from 'immutable';

import { ImmutableMap, Optional } from './utils'
import * as game from './game';
import { LEVELS } from './levels';

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals(console.log);


type PlayingState = ImmutableMap<{
  level: number,
  playerId: number | null,
  state: game.GameState,
}>;

function main() {
  // parse integer
  let menu = !localStorage.getItem('hasPlayed');
  localStorage.setItem('hasPlayed', 'true');
  menu = true;
  let solved_to = parseInt(localStorage.getItem('solved_to') || '0');
  function reset_playing_state_for_level(level: number): PlayingState {
    let state = LEVELS[level].get('initial_state');
    let playerId = 0;
    return Map({ level, playerId, state });
  }
  let playing_state: PlayingState = reset_playing_state_for_level((solved_to >= LEVELS.length) ? 0 : solved_to)
  let hist = List<PlayingState>([playing_state]);
  let current_index = 0;
  let won = false; // whether we won the level
  let err: Optional<Error> = null;
  let msg: Optional<Error> = null;
  let extra_div: React.ReactElement | null = null;

  const root = ReactDOM.createRoot(
    document.getElementById('root') as HTMLElement
  );
  function rerender() {
    let state = playing_state.get('state');
    let level = LEVELS[playing_state.get('level')];
    if (game.isWon(state))  {
      won = true;
      solved_to = Math.max(playing_state.get('level')+1, solved_to);
      localStorage.setItem('solved_to', solved_to.toString());
    }
    root.render(<React.StrictMode>
      <App
      level_header={level.get('name')}
      level_victory={solved_to > playing_state.get('level') ? level.get('flavor_text') : level.get('help_text')}
        level_render_classes={level.get('render_classes')}
        extra_div={extra_div}
        state={state} activePlayer={playing_state.get('playerId')} err={err}
        won={won} menu={menu}
        msg={msg}
      />
    </React.StrictMode>);
  }
  rerender()

  function handleAction(action: game.Action): [Optional<game.GameState>, Optional<Error>] {
    try {
      return game.tryAction(playing_state.get('state'), action);
    } catch (e) {
      err = e as Error;
      return [null, null];
    }
  }

  function handleMove(direction: string): [Optional<game.GameState>, Optional<Error>] {
    if (playing_state.get('playerId') === null) {
      return handleAction(Map({type: 'wait'}));
    }
    return handleAction(Map({type: 'move', playerId: playing_state.get('playerId'), direction}));
  }

  function update_playing_state(playing_state: PlayingState) {
    hist = hist.slice(0, current_index + 1).push(playing_state);
    current_index = hist.size - 1;
  }


  function* keyHandler(): Generator<void, void, KeyboardEvent> {
    while (true) {
      let e = yield;
      msg = null;
      err = null;
      if (menu) {
        menu = false;
        continue;
      }
      if (won && e.key !== 'z') {
        // if (cur_level_i < LEVELS.length - 1) {
        won = false;
        playing_state = reset_playing_state_for_level((playing_state.get('level') + 1) % LEVELS.length);
        update_playing_state(playing_state);
        continue;
      }
      if (e.key === 'z' && current_index > 0) {
        current_index--;
        let res = hist.get(current_index);
        if (res === undefined) {
          throw new Error(`undefined history at ${current_index}`);
        }
        playing_state = res;
        continue;
      } else if (e.key === 'Z' && current_index < hist.size - 1) {
        current_index++;
        let res = hist.get(current_index);
        if (res === undefined) {
          throw new Error(`undefined history at ${current_index}`);
        }
        playing_state = res;
        continue;
      } else if (e.key === 'p' || e.key === 'Enter') {
        menu = true;
        continue;
      } else if (e.key === 'b') {
        let cur_level_i = playing_state.get('level');
        if (cur_level_i > 0) {
          cur_level_i--;
        } else if (solved_to > LEVELS.length - 1) {
          cur_level_i = LEVELS.length - 1;
        } else {
          continue;
        }
        playing_state = reset_playing_state_for_level(cur_level_i);
        update_playing_state(playing_state);
        continue;
      } else if (e.key === 'n') {
        let cur_level_i = playing_state.get('level');
        if (cur_level_i < LEVELS.length - 1 && solved_to > cur_level_i) {
          cur_level_i++;
        } else if (solved_to > LEVELS.length - 1) {
          cur_level_i = 0;
        } else {
          continue;
        }
        playing_state = reset_playing_state_for_level(cur_level_i);
        update_playing_state(playing_state);
        continue;
      } else if (e.key === 'x') {
        let existing_players = game.existingPlayers(playing_state.get('state'))
        let playerId: number | null = null;
        if (existing_players.size === 0) {
          playerId = null;
        } else {
          let next_i = (existing_players.indexOf(playing_state.get('playerId') as number) + 1) % existing_players.size;
          playerId = existing_players.get(next_i) as number;
        }
        console.log('cycled to', playerId);
        playing_state = playing_state.set('playerId', playerId);
        update_playing_state(playing_state);
        continue;
      } else if (e.key === 'r') {
        playing_state = reset_playing_state_for_level(playing_state.get('level'));
        update_playing_state(playing_state);
        continue;
      }

      let new_state: Optional<game.GameState> = null;
      if (e.key === 'ArrowUp' || e.key === 'w') {
        [new_state, msg] = handleMove('up');
      } else if (e.key === 'ArrowDown' || e.key === 's') {
        [new_state, msg] = handleMove('down');
      } else if (e.key === 'ArrowLeft' || e.key === 'a') {
        [new_state, msg] = handleMove('left');
      } else if (e.key === 'ArrowRight' || e.key === 'd') {
        [new_state, msg] = handleMove('right');
      } else if (e.key === 'c') {
        let state = playing_state.get('state');
        let creatable = game.creatables(state);
        let neg_wormholes = state.get('wormholes').filter((wormhole: game.WormholeState, id: number) => {
          return wormhole.get('outTime') < 0;
        }).keySeq().toList().sort();
        if (!neg_wormholes.size) {
          msg = new Error('No negative time wormholes to exit');
          continue;
        }
        extra_div = <div>
          <h5>What is created?</h5>
          {creatable.map((t, i) => <div key={i}>
            <div onClick={() => {
              /* TODO */
            }}>
              {i+1}. {game.item_type_display(t, false)}
            </div>
          </div>)}
        </div>;
        e = yield;
        extra_div = null;
        let i: number = parseInt(e.key); // can actually be NaN, this is a lie
        let t: game.ItemType = creatable.get(i - 1) as game.ItemType;
        if (!t) {
          msg = new Error(`Invalid selection ${e.key}`);
          continue;
        }
        let wormhole_id: number;
        if (neg_wormholes.size === 1) {
          wormhole_id = neg_wormholes.keySeq().first();
        } else {
          let keys = ['1', '2', '3', '4', '5', '6', '7', '8', '9', '0', 'a', 'b', 'c', 'd'];
          extra_div = <div>
            <h5>Where to enter?</h5>
            {neg_wormholes.map((p, i) => <div key={i}>
              <div onClick={() => {
                /* TODO */
              }}>
              {keys[i]}. <div className="cell">
                <div className="wormholeOut" style={{borderColor: wormhole_colors[p], width:'100%', height:'100%'}}>
                </div>
              </div>
              </div>
            </div>)}
          </div>;
          e = yield;
          extra_div = null;
          try {
            wormhole_id = neg_wormholes.get(keys.indexOf(e.key)) as number;
          } catch (err) {
            msg = new Error(`Invalid selection ${e.key}`);
            continue;
          }
        }
        let location: game.Location = state.getIn(['wormholes', wormhole_id, 'out']) as game.Location;
        let items = game.getCell(state.get('grid'), location).get('items');
        let direction: game.Direction | null = null;
        if (items.find(item => game.pushable(item))) {
          extra_div = <div>
            <h5>What direction to push?</h5>
          </div>;
          e = yield;
          extra_div = null;
          direction = ({w: 'up', a: 'left', s: 'down', d: 'right'}[e.key] || 'up') as game.Direction;
        }
        [new_state, msg] = handleAction(Map({type: 'enter', item: t, wormhole_id: wormhole_id, direction: direction}))
      } else if (e.key === ' ') {
        [new_state, msg] = handleAction(Map({type: 'wait'}));
      } else {
        continue;
      }
      if (msg === null) {
        if (new_state === null) {
          continue;
        }
        playing_state = playing_state.set('state', new_state);
        (window as any).state = new_state;
        let cur_player = playing_state.get('playerId');
        if (new_state.getIn(['players', cur_player, 'location']) === null || cur_player === null) {
          let existing_players = game.existingPlayers(new_state);
          let new_p: number | null = null;
          if (existing_players.size > 0) {
            new_p = existing_players.first();
          }

          playing_state = playing_state.set('playerId', new_p);
        }
        update_playing_state(playing_state);
      }
    }
  }

  const handler = keyHandler();
  handler.next()
  document.addEventListener('keydown', (e) => {handler.next(e); rerender();});
}
main()
